@import 'src/assets/styles/mixins';
@import 'src/assets/styles/variables';

.header {
	height: 113px;
	padding: 30px 0;
	@include center(flex, center, space-between);
	text-transform: capitalize;

	.welcome {
		.text {
			margin-bottom: 5px;
			font-size: 12px;
			color: $gray-light;
		}
		.user {
			color: #1d1617;
			font-size: 20px;
			font-weight: $bold;
		}
	}

	.pageTitle {
		color: $black;
		font-weight: $bold;
	}

	.buttonBack {
		width: 32px;
		height: 32px;
	}
}

@media (max-width: $tablet) {
	.header {
		height: 113px;
		padding: 15px 0;
	}
}
